import { Component, ChangeDetectionStrategy, ViewChild, ChangeDetectorRef } from "@angular/core";
import { Platform, Form, ViewController } from "ionic-angular";


import * as moment from "moment-mini";

import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsCreditCardComponent } from '@components/mars-credit-card/mars-credit-card';
import { MarsGeolocationService } from '@services/geolocation/geolocation.service';
import { MarsAuthService } from '@services/auth.service';
import { Backend } from '@backend/index';


@Component({
    selector: "page-payment-card",
    templateUrl: "payment-card.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class PaymentCardPage {

    isMerchant: boolean;
    @ViewChild('form') form: any;
    @ViewChild('cardForm') cardForm: MarsCreditCardComponent;
    translations: AppTranslations;
    token: string;
    CARD_INFO = 0;
    HOLDER_INFO = 1;
    active = 0;
    geolocation: any = {};
    paymentMonths: Array<any>;
    paymentYears: Array<any>;
    card = {
        brand: "",
        number: "",
        cvv: "",
        expirationMonth: "",
        expirationYear: "",
        token: "",
        holder: {
            name: "",
            email: "",
            birth: "",
            birthDate: new Date(),
            document: {
                type: "CPF",
                number: ""
            },
            phone: "",
            address: {
                street: "",
                number: "",
                neighbourhood: "",
                city: "",
                state: "",
                country: "",
                postalCode: ""
            }
        }
    };

    constructor(private platform: Platform,
        private viewCtrl: ViewController,
        private changeDetector: ChangeDetectorRef,
        private locales: AppLocales,
        private geolocationService: MarsGeolocationService,
        private interactionService: MarsInteractionService,
        private globals: AppGlobals) {
        this.translations = this.locales.load();
        this.geolocation.isReady = MarsGeolocationService.isReady;
    }

    ionViewWillEnter() {
        this.init();
    }

    async init() {
        this.card = this.globals.paymentInstrument || this.card;
        if (MarsAuthService.isLoggedIn()) {
            this.isMerchant = MarsAuthService.hasRole("MERCHANT");
            this.token = MarsAuthService.getMarsToken();
            let user = MarsAuthService.getLoggedInUser();
            this.card.holder.phone = user.phone;
            this.card.holder.email = user.email;
            this.card.holder.document = user.documents[0];
            if (MarsAuthService.hasRole("CUSTOMER")) {
                user.birthDate = new Date(user.birthDate);
                this.card.holder.birthDate = user.birthDate.toLocaleDateString("pt");
                this.card.holder.name = user.name;
            }
        }
    }

    ionViewDidEnter() { }

    clear() {
        this.card.holder.name = "";
        this.card.holder.email = "";
        this.card.holder.email = "";
        this.card.holder.birth = "";
        this.card.holder.phone = "";
        this.card.holder.document = { type: "", number: "" };
    }

    getPhoneMask(phone) {
        return (phone && phone.length > 14) ? '(99) 99999-9999' : '(99) 9999-9999'
    }

    getDocumentMask(documentType: string) {
        return documentType == "CNPJ" ? "99.999.999/9999-99" : "999.999.999-99";
    }

    async save() {
        let spinner = this.interactionService.spinner({ content: this.translations.loading + "..." });
        try {
            let card = Object.assign(this.card);
            card.holder.birthDate = moment(this.card.holder.birthDate, "DD/MM/YYYY").toDate();
            let instruments = await Backend.addPaymentCard({ card: this.card, xAccessToken: this.token });
            this.viewCtrl.dismiss({ instruments: instruments, card: this.card });
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.changeDetector.detectChanges();
            spinner.dismiss();
        }
    }

    disableSubmit() {
        return this.form && !this.form.valid;
    }
}



